@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@400;700&family=Roboto:wght@400;500&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	background-color: #1e1d2b;
}

.app {
	display: flex;
	margin: 0 auto;
	max-width: 100vw;
	max-width: 1920px;
	height: 100vh;
	background-color: #1e1d2b;
}

@media screen and (max-width: 870px) {
	.app {
		display: flex;
		flex-direction: column;
		margin: 0 auto;
		height: 100vh;
		background-color: #1e1d2b;
	}
}

.menu__container{height:100vh;min-width:100px;border-right:3px solid #3c3f57;display:flex;justify-content:center;align-items:center;flex-direction:column}@media screen and (max-width: 1550px){.menu__container{position:fixed;left:0;top:0}}@media screen and (max-width: 870px){.menu__container{width:100%;height:100px;flex-direction:row;background-color:#3c3f57;z-index:3}}.menu__item{width:100%;padding:1rem 0;border-radius:10px;cursor:pointer;display:flex;flex-direction:column;align-items:center}.menu__item svg{width:50px;height:50px;color:white}.menu__item svg .active{color:#252736}.menu__item:hover{background-color:#252736}.active svg{color:#0177fb}.active h6{color:#0177fb}

.dashboard__wrapper{display:flex;flex-direction:column;width:100%}.dashboard__wrapper header{border-bottom:3px solid #3c3f57;padding:20px 0;margin:0 50px}@media screen and (max-width: 1550px){.dashboard__wrapper header{flex-direction:column;margin-left:150px;margin-right:400px}}@media screen and (max-width: 870px){.dashboard__wrapper header{margin:100px 50px 0 50px;text-align:center}}.dashboard__container{display:flex;justify-content:space-between;width:calc(100% - 100px);margin:50px}@media screen and (max-width: 1550px){.dashboard__container{width:calc(100% - 560px);flex-direction:column;margin-left:150px;margin-right:auto}}@media screen and (max-width: 870px){.dashboard__container{width:calc(100% - 100px);margin:0 50px;justify-content:start}}.left__side{width:55%;display:flex;flex-direction:column;color:white}@media screen and (max-width: 1550px){.left__side{width:100%;flex:1 1}}@media screen and (max-width: 870px){.left__side{max-width:100%;flex:1 1}}.right__side{color:white;width:40%}@media screen and (max-width: 1550px){.right__side{width:100%}}.dasboard__cards{display:flex;width:100%;justify-content:space-between;margin:50px 0}

.worldstats__container{background-color:#252736;min-width:350px;height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;border-radius:10px;z-index:2}@media screen and (max-width: 1550px){.worldstats__container{position:fixed;right:0;top:0;width:350px}}@media screen and (max-width: 870px){.worldstats__container{position:relative;bottom:0;left:0;width:100%;height:500px;margin-top:50px}}.worldstats__board{margin:50px 0}@media screen and (max-width: 870px){.worldstats__board{margin:20px 0}}.worldstats__today-stats{width:100%;margin:50px 0}@media screen and (max-width: 870px){.worldstats__today-stats{margin:20px 0}}

