@import url("https://fonts.googleapis.com/css2?family=Exo+2:wght@400;700&family=Roboto:wght@400;500&display=swap");

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	background-color: #1e1d2b;
}

.app {
	display: flex;
	margin: 0 auto;
	max-width: 100vw;
	max-width: 1920px;
	height: 100vh;
	background-color: #1e1d2b;
}

@media screen and (max-width: 870px) {
	.app {
		display: flex;
		flex-direction: column;
		margin: 0 auto;
		height: 100vh;
		background-color: #1e1d2b;
	}
}
